/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { useDispatch } from '@wordpress/data';

/**
 * External dependencies
 */
import { STORE_NAME as NAB_DATA, usePageAttribute } from '@nab/data';

/**
 * Internal dependencies
 */
import './style.scss';
import Logo from '../../../../../images/full-logo.svg';

import { WelcomeNewUser } from './new-user';
import { WelcomeOldUserWithExperiments } from './old-user-with-experiments';
import { WelcomeOldUserWithNoExperiments } from './old-user-with-no-experiments';

import { ExperimentMigration } from '../experiment-migration';

export type WelcomeBoxProps = {
	readonly isOldSubscriber: boolean;
	readonly oldExperiments: ReadonlyArray< number >;
};

export const WelcomeBox = ( {
	isOldSubscriber,
	oldExperiments,
}: WelcomeBoxProps ): JSX.Element => {
	const [
		isMigratingExperiments,
		migrateExperiments,
	] = useExperimentMigrator();
	const [ isInitializingPlugin, initPlugin ] = usePluginInitializer(
		isOldSubscriber
	);
	const [ isPolicyAccepted, togglePolicy ] = usePageAttribute(
		'welcome/isPolicyAccepted',
		false
	);
	const [ isBackupAccepted, toggleBackup ] = usePageAttribute(
		'welcome/isBackupWarningAccepted',
		false
	);

	if ( isMigratingExperiments ) {
		return <ExperimentMigration experiments={ oldExperiments } />;
	} //end if

	return (
		<div className="nab-welcome-message">
			<Logo
				className="nab-welcome-message__logo"
				title="Nelio A/B Testing"
				alt="Nelio A/B Testing"
			/>

			{ ! isOldSubscriber && (
				<WelcomeNewUser
					initPluginInAws={ initPlugin }
					isPluginBeingInitialized={ isInitializingPlugin }
					isPolicyAccepted={ isPolicyAccepted }
					togglePolicy={ togglePolicy }
				/>
			) }

			{ isOldSubscriber && ! oldExperiments.length && (
				<WelcomeOldUserWithNoExperiments
					initPluginInAws={ initPlugin }
					isPluginBeingInitialized={ isInitializingPlugin }
				/>
			) }

			{ isOldSubscriber && !! oldExperiments.length && (
				<WelcomeOldUserWithExperiments
					isPluginBeingInitialized={ isInitializingPlugin }
					migrateExperiments={ migrateExperiments }
					isBackupWarningAccepted={ isBackupAccepted }
					toggleBackupWarning={ toggleBackup }
				/>
			) }
		</div>
	);
};

// =====
// HOOKS
// =====

const usePluginInitializer = ( isOldSubscriber ) => {
	const [ initting, markAsInitting ] = usePageAttribute(
		'welcome/isPluginBeingInitialized',
		false
	);
	const {
		createFreeSite,
		createPremiumSite,
		removeOldAccountData,
	} = useDispatch( NAB_DATA );

	const createSite = isOldSubscriber ? createPremiumSite : createFreeSite;

	const init = () => {
		markAsInitting( true );
		void createSite()
			.then( () => removeOldAccountData() )
			.then( () => window.location.reload() );
	};
	return [ initting, init ] as const;
};

const useExperimentMigrator = () => {
	// eslint-disable-next-line @typescript-eslint/no-unused-vars
	const [ _, markAsInitting ] = usePageAttribute(
		'welcome/isPluginBeingInitialized',
		false
	);
	const [ migrating, markAsMigrating ] = usePageAttribute(
		'welcome/areExperimentsBeingMigrated',
		false
	);
	const { createPremiumSite } = useDispatch( NAB_DATA );

	const migrate = () => {
		markAsInitting( true );
		void createPremiumSite()
			.then( () => markAsMigrating( true ) )
			.then( () => markAsInitting( false ) );
	};
	return [ migrating, migrate ] as const;
};
