/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { Button } from '@wordpress/components';
import { useDispatch } from '@wordpress/data';
import { useEffect } from '@wordpress/element';
import { _x, sprintf } from '@wordpress/i18n';

/**
 * External dependencies
 */
import classnames from 'classnames';
import { map, without } from 'lodash';
import interpolateComponents from '@automattic/interpolate-components';
import { STORE_NAME as NAB_DATA, usePageAttribute } from '@nab/data';
import { isEmpty } from '@nab/utils';

/**
 * Internal dependencies
 */
import './style.scss';

import Logo from '../../../../../images/full-logo.svg';

export type ExperimentMigrationProps = {
	readonly experiments: ReadonlyArray< number >;
};

export const ExperimentMigration = ( {
	experiments,
}: ExperimentMigrationProps ): JSX.Element => {
	const [ isInitializingPlugin, initPlugin ] = usePluginInit();

	const { count, total, perc, done } = useMigrationStats( experiments );
	useMigrateNextExperimentEffect( experiments );

	return (
		<div className="nab-welcome-message">
			<Logo
				className="nab-welcome-message__logo"
				title="Nelio A/B Testing"
				alt="Nelio A/B Testing"
			/>

			{ ! done ? (
				<p>
					{ interpolateComponents( {
						mixedString: sprintf(
							/* translators: 1 -> current test index, 2 -> total number of tests */
							_x(
								'Updating test %1$s of %2$s…',
								'text',
								'nelio-ab-testing'
							),
							`{{strong}}${ count + 1 }{{/strong}}`,
							`{{strong}}${ total }{{/strong}}`
						),
						components: {
							strong: <strong />,
						},
					} ) }
				</p>
			) : (
				<p>
					<strong>
						{ _x(
							'All tests were successfully updated!',
							'text',
							'nelio-ab-testing'
						) }
					</strong>
				</p>
			) }

			<div className="nab-migrate-experiment__progress-bar-holder">
				<div
					className={ classnames( [
						'nab-migrate-experiment__progress-bar',
						{
							'nab-migrate-experiment__progress-bar--completed': done,
						},
					] ) }
					style={ {
						width: `${ Math.min( 100, perc ) }%`,
					} }
				></div>
			</div>

			<div className="nab-welcome-message__actions">
				<Button
					className="nab-welcome-message__start"
					isPrimary
					disabled={ isInitializingPlugin || ! done }
					onClick={ initPlugin }
				>
					{ isInitializingPlugin
						? _x( 'Loading…', 'text', 'nelio-ab-testing' )
						: _x( 'View Tests »', 'command', 'nelio-ab-testing' ) }
				</Button>
			</div>
		</div>
	);
};

// =====
// HOOKS
// =====

const useMigrateNextExperimentEffect = ( experiments ) => {
	const [ migratedExperiments ] = usePageAttribute(
		'welcome/migratedExperiments',
		[]
	);
	const { migrateOldExperiment } = useDispatch( NAB_DATA );

	useEffect( () => {
		const experimentsToMigrate = without(
			map( experiments, 'ID' ),
			...migratedExperiments
		);

		if ( isEmpty( experimentsToMigrate ) ) {
			return;
		} //end if

		void migrateOldExperiment( experimentsToMigrate[ 0 ] );
	}, [ JSON.stringify( migratedExperiments ) ] );
};

const useMigrationStats = ( experiments: ReadonlyArray< number > ) => {
	const [ migratedExperiments ] = usePageAttribute(
		'welcome/migratedExperiments',
		[]
	);

	const count = migratedExperiments.length;
	const total = experiments.length;
	const perc = Math.floor( ( 100 * ( count + 1 ) ) / ( total + 1 ) );
	const done = count === total;

	return { count, total, perc, done };
};

const usePluginInit = () => {
	const [ isInitting, markAsInitting ] = usePageAttribute(
		'welcome/isPluginBeingInitialized',
		false
	);
	const { removeOldAccountData } = useDispatch( NAB_DATA );

	const init = () => {
		markAsInitting( true );
		void removeOldAccountData().then( () => window.location.reload() );
	};
	return [ isInitting, init ] as const;
};
