/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { Button, ExternalLink } from '@wordpress/components';
import { _x, sprintf } from '@wordpress/i18n';

/**
 * External dependencies
 */
import interpolateComponents from '@automattic/interpolate-components';
import { addFreeTracker } from '@nab/utils';

export type WelcomeOldUserWithNoExperimentsProps = {
	readonly initPluginInAws: () => void;
	readonly isPluginBeingInitialized: boolean;
};

export const WelcomeOldUserWithNoExperiments = ( {
	initPluginInAws,
	isPluginBeingInitialized,
}: WelcomeOldUserWithNoExperimentsProps ): JSX.Element => (
	<>
		<p className="nab-welcome-message__intro">
			{ interpolateComponents( {
				mixedString: sprintf(
					/* translators: plugin name” */
					_x(
						'Welcome to the new version of %s!',
						'text',
						'nelio-ab-testing'
					),
					'{{strong}}Nelio A/B Testing{{/strong}}'
				),
				components: { strong: <strong /> },
			} ) }
		</p>

		<p className="nab-welcome-message__info">
			{ interpolateComponents( {
				mixedString: _x(
					'There are a number of updates in this version that we hope you will like. Visit our {{help}}Knowledge Base{{/help}} for more details about Nelio A/B Testing.',
					'user',
					'nelio-ab-testing'
				),
				components: {
					help: (
						<ExternalLink
							href={ addFreeTracker(
								_x(
									'https://neliosoftware.com/testing/help/',
									'text',
									'nelio-ab-testing'
								)
							) }
						/>
					),
				},
			} ) }
		</p>

		<div className="nab-welcome-message__actions">
			<Button
				className="nab-welcome-message__start"
				isPrimary
				disabled={ isPluginBeingInitialized }
				onClick={ () => initPluginInAws() }
			>
				{ isPluginBeingInitialized
					? _x( 'Loading…', 'text', 'nelio-ab-testing' )
					: _x( 'Continue »', 'command', 'nelio-ab-testing' ) }
			</Button>
		</div>
	</>
);
