/**
 * WordPress dependencies
 */
import * as React from '@wordpress/element';
import { Button, CheckboxControl, ExternalLink } from '@wordpress/components';
import { _x, sprintf } from '@wordpress/i18n';

/**
 * External dependencies
 */
import interpolateComponents from '@automattic/interpolate-components';
import { addFreeTracker } from '@nab/utils';

export type WelcomeOldUserWithExperimentsProps = {
	readonly isBackupWarningAccepted: boolean;
	readonly isPluginBeingInitialized: boolean;
	readonly migrateExperiments: () => void;
	readonly toggleBackupWarning: ( val: boolean ) => void;
};

export const WelcomeOldUserWithExperiments = ( {
	isBackupWarningAccepted,
	isPluginBeingInitialized,
	migrateExperiments,
	toggleBackupWarning,
}: WelcomeOldUserWithExperimentsProps ): JSX.Element => (
	<>
		<p className="nab-welcome-message__intro">
			{ interpolateComponents( {
				mixedString: sprintf(
					/* translators: plugin name (Nelio A/B Testing) */
					_x(
						'Welcome to the new version of %s!',
						'text',
						'nelio-ab-testing'
					),
					'{{strong}}Nelio A/B Testing{{/strong}}'
				),
				components: { strong: <strong /> },
			} ) }
		</p>

		<p className="nab-welcome-message__info">
			{ interpolateComponents( {
				mixedString: _x(
					'There are a number of updates in this version that we hope you will like. However, we must first update your tests. Please backup your site and proceed with the update. Visit our {{help}}Knowledge Base{{/help}} for more details.',
					'user',
					'nelio-ab-testing'
				),
				components: {
					help: (
						<ExternalLink
							href={ addFreeTracker(
								_x(
									'https://neliosoftware.com/testing/help/how-to-upgrade-to-nelio-ab-testing-5/',
									'text',
									'nelio-ab-testing'
								)
							) }
						/>
					),
				},
			} ) }
		</p>

		<div className="nab-welcome-message__backup-warning">
			<CheckboxControl
				label={ _x(
					'I already have a backup of my site.',
					'text',
					'nelio-ab-testing'
				) }
				checked={ isBackupWarningAccepted }
				disabled={ isPluginBeingInitialized }
				onChange={ toggleBackupWarning }
			/>
		</div>

		<div className="nab-welcome-message__actions">
			<Button
				className="nab-welcome-message__start"
				isPrimary
				disabled={
					! isBackupWarningAccepted || isPluginBeingInitialized
				}
				onClick={ migrateExperiments }
			>
				{ isPluginBeingInitialized
					? _x( 'Initializing…', 'text', 'nelio-ab-testing' )
					: _x( 'Update Tests »', 'command', 'nelio-ab-testing' ) }
			</Button>
		</div>
	</>
);
